@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff"); }

.page-header {
  display: flex;
  align-items: center;
  height: 90px; }
  @media (max-width: 1023px) {
    .page-header {
      height: 69px; } }
  @media (max-width: 767px) {
    .page-header {
      height: 48px; } }

.logo {
  display: block;
  position: relative;
  top: -5px;
  height: 30px;
  margin-left: 60px;
  margin-right: 60px; }
  @media (max-width: 1023px) {
    .logo {
      top: 2px;
      margin-left: 45px;
      margin-right: 66px; } }
  @media (max-width: 767px) {
    .logo {
      margin-left: 15px;
      margin-right: 15px; } }

.logo__image {
  width: 149px;
  height: 30px; }
  @media (max-width: 1023px) {
    .logo__image {
      width: 133px;
      height: 24px; } }
  @media (max-width: 767px) {
    .logo__image {
      width: 118px;
      height: 21px; } }

.menu {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none; }
  @media (max-width: 767px) {
    .menu {
      position: relative;
      order: -1;
      width: 16px;
      height: 10px;
      margin-left: 15px;
      cursor: pointer; }
      .menu .menu__item a {
        display: none; }
      .menu .menu__item {
        position: absolute;
        width: 16px;
        height: 1px;
        background-color: #1F1E25;
        transition: background-color 0.3s ease; }
      .menu .menu__item:first-child {
        top: 0px; }
      .menu .menu__item:nth-child(3) {
        top: 10px; }
      .menu:hover .menu__item {
        background-color: #2C36F2; } }

.menu__item {
  margin-right: 25px; }
  @media (max-width: 1023px) {
    .menu__item {
      margin-right: 20px; } }

.menu__ref {
  color: #1F1E25;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  text-decoration: none;
  transition: color 0.3s ease; }
  @media (max-width: 1023px) {
    .menu__ref {
      font-size: 14px; } }
  .menu__ref:hover {
    color: #2C36F2; }

.enter {
  position: relative;
  width: 20px;
  height: 22px;
  margin-right: 280px;
  margin-left: auto;
  padding: 0;
  background: url("../img/enter-icon.svg") no-repeat;
  border: none;
  outline: none;
  cursor: pointer;
  transition: transform 0.3s ease; }
  .enter:hover {
    transform: translateX(5px); }
  @media (max-width: 1023px) {
    .enter {
      margin-right: 45px; } }
  @media (max-width: 767px) {
    .enter {
      width: 17px;
      height: 16px;
      margin-right: 8px;
      background-size: contain; } }
  .enter:before {
    content: 'Войти в Интернет-банк';
    position: absolute;
    top: 3px;
    left: 30px;
    font-family: "Roboto", sans-serif;
    white-space: nowrap; }
    @media (max-width: 1023px) {
      .enter:before {
        content: ''; } }

.loginPopup {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1001;
  visibility: hidden;
  box-sizing: border-box;
  width: 544px;
  height: 493px;
  padding: 56px;
  border: 4px solid #2C36F2;
  opacity: 0;
  background-color: #F6F7FF;
  transform: translate(-50%, -40%);
  transition: opacity 0.3s, transform 0.3s; }
  @media (max-width: 1023px) {
    .loginPopup {
      width: 678px;
      padding: 56px 84px; } }
  @media (max-width: 767px) {
    .loginPopup {
      width: calc(100% - 30px);
      height: 455px;
      padding: 44px 17px; } }

.loginPopup_open {
  opacity: 1;
  transform: translate(-50%, -50%); }

.loginPopup__header {
  display: flex;
  margin-bottom: 33px; }
  @media (max-width: 767px) {
    .loginPopup__header {
      margin-bottom: 25px; } }

@media (max-width: 767px) {
  .logoInternetBank {
    position: relative;
    right: 6px; } }

.close {
  position: relative;
  top: -2px;
  left: 1px;
  width: 20px;
  height: 20px;
  margin-left: auto;
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer; }
  .close:before {
    content: '';
    position: absolute;
    top: 0;
    width: 2px;
    height: 23px;
    background-color: #1F1E25;
    transform: rotate(45deg); }
  .close:after {
    content: '';
    position: absolute;
    top: 0;
    width: 2px;
    height: 23px;
    background-color: #1F1E25;
    transform: rotate(-45deg); }

.loginForm {
  display: flex;
  flex-direction: column; }

.login,
.password__input,
.enterButton {
  width: 100%;
  height: 60px;
  padding: 0;
  border: 1px solid #1F1E25;
  border-radius: 4px;
  outline: none; }

.login {
  margin-bottom: 26px; }
  @media (max-width: 767px) {
    .login {
      margin-bottom: 21px; } }

.login:focus,
.password__input:focus {
  transition: border-color 0.3s;
  border-color: #2C36F2; }

.login,
.password__input {
  padding: 10px 20px;
  font-size: 20px;
  background-color: #F6F7FF; }

.password {
  position: relative;
  display: flex;
  flex-direction: column; }

.password__forget {
  align-self: flex-end;
  margin-top: 8px;
  margin-bottom: 29px;
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  text-decoration: none;
  color: #707C87; }
  @media (max-width: 767px) {
    .password__forget {
      position: relative;
      top: 85px;
      align-self: center;
      margin-bottom: 6px;
      font-size: 14px; } }

.enterButton {
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  color: #F6F7FF;
  border: none;
  background-color: #2C36F2;
  cursor: pointer; }
  @media (max-width: 767px) {
    .enterButton {
      height: 50px;
      font-size: 14px; } }

.loginForm label {
  margin-bottom: 7px;
  font-family: "Roboto", sans-serif;
  font-size: 16px; }

.password__show {
  position: absolute;
  top: 24px;
  right: 24px;
  width: 22px;
  height: 12px;
  background: url("../../img/showPassword-icon.svg");
  cursor: pointer; }
  @media (max-width: 767px) {
    .password__show {
      right: 15px; } }

.password__show_hold:after {
  content: "";
  position: relative;
  top: 0px;
  left: 8px;
  display: block;
  width: 5px;
  height: 5px;
  background-color: #1F1E25;
  border-radius: 100%; }

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3); }

* {
  box-sizing: border-box; }
